body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

.nav-bar-info {
  position: absolute;
  top: 5%;
  right: 0;
  left: 39%;
  display: table;
  margin: 0 auto;
  transform: translateY(-50%);
}

@media screen and (max-width: 400px) {
  .nav-bar-info {
    position: absolute;
    top: 5%;
    right: 0;
    left: 10%;
    display: table;
  }
}

.nav-bar-info a {
  position: relative;
  width: 33.333%;
  display: table-cell;
  text-align: center;
  color: #555555;
  text-decoration: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  padding: 10px 20px;
  transition: 0.4s ease color;
}

.nav-bar-info a:before, nav a:after {
  content: "";
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  transition: 0.3s ease transform;
}

.nav-bar-info a:before{
  top: 0;
  left: 10px;
  width: 6px;
  height: 6px;
}

.nav-bar-info a:after{
  top: 5px;
  left: 18px;
  width: 4px;
  height: 4px
}

.nav-bar-info a:nth-child(1):before
{
  background-color: rgb(52, 36, 139);
}

.nav-bar-info a:nth-child(1):after
{
  background-color: red;
}

.nav-bar-info a:nth-child(2):before
{
  background-color: #ff0404;
}

.nav-bar-info a:nth-child(2):after
{
  background-color: #080c04;
}

.nav-bar-info a:nth-child(3):before
{
  background-color: purple;
}

.nav-bar-info a:nth-child(3):after
{
  background-color: palevioletred;
}

#indicator {
  position: absolute;
  left: 5%;
  bottom: 0;
  width: 30px;
  height: 3px;
  background-color: #fff;
  border-radius: 5px;
  transition: 0.2s ease left;
}

.nav-bar-info a:hover
{
  color: rgb(0, 0, 0);
}

.nav-bar-info a:hover:before, nav a:hover:after
{
  transform: scale(1);
}

.nav-bar-info a:nth-child(1):hover ~ #indicator
{
  background: anear-gradient(130deg, yellow, red);
}

.nav-bar-info a:nth-child(2):hover ~ #indicator
{
  left: 34%;
  background: anear-gradient(130deg, #00e2ff, #89ff00);
}

.nav-bar-info a:nth-child(3):hover ~ #indicator
{
  left: 70%;
  background: anear-gradient(130deg, purple, palevioletred);
}
